.pop-blanket {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  z-index: 8;
}

.popup-container {
  background: var(--white2);
  width: 980px;
  height: 600px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 12px;
  z-index: 8;
  position: relative;
  /* margin-top: 68px; */
}

.content-box {
  padding-top: 80px;
  height: 600px;
  overflow: auto;
  max-width: 940px;
    
    margin: 0 auto;
}
.popup-header {
  display: grid;
  grid-template-columns: 1fr 54px;
  grid-template-rows: 38px;
  justify-content: space-between;
  align-items: center;
  padding: 10px 24px;
  position: fixed;
  width: 100%;
  background: #fff;
  z-index: 3;
  border-radius: 12px;
}

@media (max-width:980px){
    .popup-container{
        width:100%;
        height:100vh;
    }
    .content-box {
  padding-top: 80px;
  height: 100vh;
  overflow: auto;
}
}